<template>
  <div class="container-fluid dashboard">
    <h1 class="text-primary">Dashboard</h1>
    <a href="#" v-on:click="logout">Logout</a>
  </div>
</template>

<script>
import router from "../router.js";
import axios from "axios";
export default {
  name: "dashboard",
  methods: {
    logout: function() {
      axios.get("/logout").then(() => {
        router.push("/");
      });
    }
  }
};
</script>

<style scoped>
.dashboard {
  text-align: center;
}
</style>
