import Vue from "vue";
import Router from "vue-router";
import login from "./components/loginPage.vue";
import dashboard from "./components/dashboard.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "login",
      component: login
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: dashboard
    }
  ]
});
